html {
  --main-color: #00acdc;
  --font-size: 12px;
  --font-weight: 500;
  --line-height: 14px;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: 40px;
  margin: 10px 0;
}

.logo a {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  font-size: var(--font-size);
  font-weight: var(--font-weight);
  line-height: var(--line-height);
  align-items: center;
  height: 14px;
  padding: 23px 0;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--main-color);
  transition: color .15s;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--main-color);
  position: relative;
}
